#contactPages{
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 65rem;
    text-align: center;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.clientPagetitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}
.contacPageDes{
    font-size: 1rem;
    font-weight: 300;
}
.clientImgs{
    margin: 1rem 0;
    display: flex;
}
.clientImg{
    object-fit: cover;
    width: 25%;
    min-width: 9rem;
    margin: auto;
    padding: 0.25rem;
}
#client{
    padding: 2rem;
}
.conatactDes{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}
.contactForm{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}
.name, .email, .msg{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: rgb(40, 40, 40);
}
.submitbtn{
    background: white;
    border: none;
    border-radius: 0.5rem;
    padding: 0.75rem 3.5rem;
    margin: 2rem;
}
.links{
    display: flex;
    flex-wrap: wrap;
}
.link{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}